@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
p {
  text-align: left;
  font-family: "Nunito", sans-serif;
}

h2,
h3,
ion-title {
  font-family: "Montserrat", sans-serif;
  font-weight: bolder;
}

:root {
  --ion-safe-area-top: 20px;
  --ion-safe-area-bottom: 22px;
}

.swiper-slide {
  display: block;
}

ion-slide > h2 {
  margin-top: 2.8rem;
}

ion-slide > img {
  animation: fade-in 2.5s cubic-bezier(0.39, 0.575, 0.565, 1) both;

  height: 6em !important;
  margin-top: 1em;
}

swiper-slide > img {
  animation: fade-in 2.5s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  height: 6em !important;
  margin-top: 1em;
}

.vbox-main-color1 {
  color: #a11e46;
}

.logoimg {
  height: 32px;
}

.hidden {
  display: none;
}

[hoverable]:hover {
  background: #eee;
  cursor: pointer;
}

.h-30 {
  height: 128px;
  object-fit: contain;
  pointer-events: none;
}

[hoverable] * {
  pointer-events: none;
}

.scene {
  border: none;
  width: 100%;
  height: 100%;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.hidden-header {
  display: none !important;
}

.welcome-screen {
  width: 100%;
  min-width: 240px !important;
  min-height: 80vh;
  background-image: url("/assets/img/v2-welcome-screen-top-and-left.png");
  background-size: 100% 411px;
  background-repeat: no-repeat;
}

.welcome-screen .img-illustration {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}

.welcome-screen .img-illustration .img-boy {
  height: 180px;
  object-fit: contain;
}

.welcome-screen .img-illustration .img-logo {
  height: 120px;
  padding: 1.5em 0 1em 0;
  object-fit: contain;
}

.illustration-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  min-height: 400px;
  min-width: 400px;
  max-height: 600px;
  max-width: 600px;
}

h3 {
  margin-top: 20px;
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: bolder;
  white-space: nowrap;
  font-size: 20pt;
  line-height: 0em;
  color: var(--ion-color-primary);
  margin-bottom: 1.5em;
}

b {
  color: var(--ion-color-primary);
}
.intro-content {
  padding: 0 2em;
  font-size: 0.8em;
  max-width: 640px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.intro-content p {
  text-align: left;
  font-family: "Nunito", sans-serif;
}

.intro-screen {
  width: 100%;
  min-width: 240px !important;
  min-height: 80vh;
  background-image: url("/assets/img/intro-screen-bg.png");
  background-size: 100% 600px;
  background-repeat: no-repeat;
}

.intro-screen .img-illustration {
  background-image: url("/assets/img/stars.png");
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}

.intro-screen .img-illustration .img-planets {
  height: 180px;
  object-fit: contain;
  margin-bottom: 110px;
}
.landmark-img {
  background-color: var(--ion-color-primary);
  margin-top: 10px;
  width: 70px;
  height: 70px;
  padding: 10px;
  margin-bottom: -20px;
  border-radius: 50px;
}
.coming-soon-text {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 150px;
  border: 2px solid var(--ion-color-primary);
  border-radius: 50px;
  color: var(--ion-color-primary);
  margin-top: 30px;
}
.ready-screen {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 100vw;
  min-width: 240px !important;
  min-height: 100vh !important;
  height: 100% !important;
  background-image: url("/assets/img/space-bg.png");
  background-size: contain;
  background-position: bottom;
  background-repeat: repeat-x;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
  color: #fff;
}
.ready-screen .smokes {
  overflow: hidden;
  content: "";
  position: absolute;
  bottom: 0px;
  width: 100vw;
  height: 160px;
}

div.rocket {
  display: block;
  width: 100vw;
  padding: 0px !important;
  position: absolute;
  bottom: 30px;
  z-index: 100;
}
div.rocket img {
  height: 160px;
  max-height: 25vh;
}
.ready-screen .smokes .light-smoke {
  width: 100vw;
  position: absolute;
  left: 0;
  top: 50px;
  height: 70%;
}
.ready-screen .smokes .medium-smoke {
  width: 100vw;
  position: absolute;
  left: 0;
  top: 44px;
  height: 100%;
}
.ready-screen .smokes .dark-smoke {
  width: 100vw;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}

.overlay-text-container {
  width: 100%;
  height: 5em;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.icon-container {
  align-items: center;
  justify-content: center;
  margin-right: 0.2rem;
  height: 45px;
  width: 45px;
  text-align: center;
  z-index: 11;
}
.overlay-text {
  display: flex;
  height: 2.5rem;
  justify-content: center;
  padding: 10px 25px 10px 25px;
  color: #ffffff;
  background-color: var(--ion-color-primary);
  border: solid 2px #ffffff;
  border-radius: 0 50px 0 50px;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
}
.icon-container svg {
  color: #ffffff;
  background-color: var(--ion-color-primary);
  border: solid 2px #ffffff;
  border-radius: 50px;
  padding: 10px;
}

.ready-screen > div {
  padding: 2em;
}
.swiper-container {
  height: 100vh !important;
  overflow: visible;
}
.swiper-slide {
  height: 100% !important;
  overflow-x: hidden !important;
  overflow-y: scroll !important;
}
.button-native {
  z-index: 900;
}

.home-logos-container {
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

/* swipe animation to continue */
.swipeAnim {
  animation: prompt-swipe 5s ease-in-out infinite both;
}
@keyframes prompt-swipe {
  0%,
  100% {
    transform: translateX(5);
  }
  10%,
  30%,
  50%,
  70% {
    transform: translateX(0px);
  }
  20%,
  40%,
  60% {
    transform: translateX(10px);
  }
}

/* Ionic Variables and Theming. For more info, please see:
http://ionicframework.com/docs/theming/ */

/** Ionic CSS Variables **/
:root {
  /** primary **/
  --ion-color-primary: #893eff;
  --ion-color-primary-rgb: 56, 128, 255;
  --ion-color-primary-contrast: #ffffff;
  --ion-color-primary-contrast-rgb: 255, 255, 255;
  --ion-color-primary-shade: #3171e0;
  --ion-color-primary-tint: #4c8dff;

  /** secondary **/
  --ion-color-secondary: #3dc2ff;
  --ion-color-secondary-rgb: 61, 194, 255;
  --ion-color-secondary-contrast: #ffffff;
  --ion-color-secondary-contrast-rgb: 255, 255, 255;
  --ion-color-secondary-shade: #36abe0;
  --ion-color-secondary-tint: #50c8ff;

  /** tertiary **/
  --ion-color-tertiary: #5260ff;
  --ion-color-tertiary-rgb: 82, 96, 255;
  --ion-color-tertiary-contrast: #ffffff;
  --ion-color-tertiary-contrast-rgb: 255, 255, 255;
  --ion-color-tertiary-shade: #4854e0;
  --ion-color-tertiary-tint: #6370ff;

  /** success **/
  --ion-color-success: #2dd36f;
  --ion-color-success-rgb: 45, 211, 111;
  --ion-color-success-contrast: #ffffff;
  --ion-color-success-contrast-rgb: 255, 255, 255;
  --ion-color-success-shade: #28ba62;
  --ion-color-success-tint: #42d77d;

  /** warning **/
  --ion-color-warning: #ffc409;
  --ion-color-warning-rgb: 255, 196, 9;
  --ion-color-warning-contrast: #000000;
  --ion-color-warning-contrast-rgb: 0, 0, 0;
  --ion-color-warning-shade: #e0ac08;
  --ion-color-warning-tint: #ffca22;

  /** danger **/
  --ion-color-danger: #eb445a;
  --ion-color-danger-rgb: 235, 68, 90;
  --ion-color-danger-contrast: #ffffff;
  --ion-color-danger-contrast-rgb: 255, 255, 255;
  --ion-color-danger-shade: #cf3c4f;
  --ion-color-danger-tint: #ed576b;

  /** dark **/
  --ion-color-dark: #222428;
  --ion-color-dark-rgb: 34, 36, 40;
  --ion-color-dark-contrast: #ffffff;
  --ion-color-dark-contrast-rgb: 255, 255, 255;
  --ion-color-dark-shade: #1e2023;
  --ion-color-dark-tint: #383a3e;

  /** medium **/
  --ion-color-medium: #92949c;
  --ion-color-medium-rgb: 146, 148, 156;
  --ion-color-medium-contrast: #ffffff;
  --ion-color-medium-contrast-rgb: 255, 255, 255;
  --ion-color-medium-shade: #808289;
  --ion-color-medium-tint: #9d9fa6;

  /** light **/
  --ion-color-light: #f4f5f8;
  --ion-color-light-rgb: 244, 245, 248;
  --ion-color-light-contrast: #000000;
  --ion-color-light-contrast-rgb: 0, 0, 0;
  --ion-color-light-shade: #d7d8da;
  --ion-color-light-tint: #f5f6f9;
}

@media (prefers-color-scheme: dark) {
